.participantList {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px 20px;
  margin-bottom: 30px;

  .list-header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    p {
      color: #9c9c9c;
      font-size: 13px;
      font-weight: 600;
    }

    .addMember {
      button {
        border: 1px solid #9c9c9c;
        background-color: #fff;
        width: 50px;
        height: 30px;
        border-radius: 8px;
        color: #000;
        font-weight: 400;
        cursor: pointer;
        margin-left: 3px;

        &:hover {
          font-size: 14px;
          font-weight: 700;
          border: 2px solid #00b8dd;
        }
      }

      input {
        box-sizing: border-box;
        border-style: none;
        width: 100px;
        height: 30px;
        outline: none;
        border-bottom: 1px solid #9c9c9c;
      }
    }
  }
}
